.chessGameBoard{
  background-color: red;
  width: 80vh;
  height: 80vh;
  display: grid;
  grid-template: repeat(8, 1fr) / repeat(8, 1fr);
}
.cell{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.light{
  background-color: #F5E6BF;
}
.darker {
  background-color: #66443A;
}
