@import url(https://fonts.googleapis.com/css2?family=Poppins&family=Vampiro+One&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-style: 'Poppins', sans-serif;
}
body{
  background-color: #14141A;
  overflow-x: hidden;
}
#root{
  width: 100vw;
  height: 100vh;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.watermark{
  font-family: 'Vampiro One', sans-serif;
  transform: rotate(-10deg);
  color: #FFFFFF;
  opacity: 0.05;
  position: absolute;
  z-index: -1;
  top: 10%;
  left: 0;
  font-size: 18vw;
  width: 100%;
}
.background-feature{
  position: absolute;
  width: 100vw;
  height: 70vh;
  bottom: 0;
  left: 0;
  z-index: -1;
  opacity: 0.2;
  background-image: url(/gamewave/static/media/background.0578369b.svg);
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
}

header{
  padding: 10px 0 10px 0;
  position: absolute;
  top: 0;
  left: 15vw;
  width: 70vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

header .logo{
  color: #FFFFFF;
  text-decoration: none;
  font-family: 'Vampiro One', sans-serif;
  font-size: 2em;
}
header nav{
  width: 15%;
  display: flex;
  justify-content: space-between;
}
header nav .link{
  font-style: 'Poppins', sans-serif;
  font-size: 1.2em;
  font-weight: bold;
  color: #FFFFFF;
  text-decoration: none;
}
header nav .link:hover{
  color: lightgrey;
}

.GameSelect{
  font-family: 'Poppins', sans-serif;
  width: 70vw;
  height: 80vh;
  display: flex;
  justify-content: space-between;
}
.GameSelect .card{
  background-color: #4B4B4B80;
  padding: 5% 0 5% 0;
  height: 100%;
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.GameSelect .card p{
  width: 80%;
  line-height: 1em;
  font-size: 1.2em;
  color: #FFFFFF;
  opacity: 0.8;
}
.GameSelect .card .wrapper{
  display: flex;
  height: 30%;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.GameSelect .card .wrapper .title{
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  min-width: 20vw;
  height: 3vw;
}
.GameSelect .card .img{
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  width: 7vw;
  height: 7vw;
  margin-bottom: 0.5em;
}
.GameSelect .card .wrapper .title-two{
  background-image: url(/gamewave/static/media/rubiks-title.740139a9.svg);
}
.GameSelect .card .wrapper .title-one{
  background-image: url(/gamewave/static/media/chess-title.c0ecc0ed.svg);
}
.GameSelect .card .wrapper .title-three{
  background-image: url(/gamewave/static/media/tetris-title.b06d3068.svg);
}
.GameSelect .card .card-one{
    background-image: url(/gamewave/static/media/chess-icon.5efbbd1d.svg);
}
.GameSelect .card .card-two{
    background-image: url(/gamewave/static/media/rubik-icon.57f899a9.svg);
}
.GameSelect .card .card-three{
    background-image: url(/gamewave/static/media/tetris-icon.a56384ab.svg);
}
.GameSelect .card .btn{
  font-size: 2em;
  font-weight: bold;
  background-color: transparent;
  border-radius: 1.5em;
  width: 80%;
  cursor: pointer;
  transition: all 0.5s ease;
}
.GameSelect .card .btn:active{
  border: none;
  outline: none;
}
.GameSelect .card #tetris-btn{
  color: #C79B00;
  border: 0.12em solid #C79B00;
}
.GameSelect .card #tetris-btn:hover{
  background-color: #C79B00;
  color: #FFFFFF;
}
.GameSelect .card #rubiks-btn{
  color: #4C6E87;
  border: 0.12em solid #4C6E87;
}
.GameSelect .card #rubiks-btn:hover{
  background-color: #4C6E87;
  color: #FFFFFF;
}
.GameSelect .card #chess-btn{
  color: #B74F0A;
  border: 0.12em solid #B74F0A;
}
.GameSelect .card #chess-btn:hover{
  background-color: #B74F0A;
  color: #FFFFFF;
}

#can{
  background-color: #BA000D;
  margin: 0 2rem 0 0;
}
.wrapper{
  text-align: center;
  display: flex;
  color: white;

}
.instructions{
  display: flex;
  width: 14vw;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.arrows{
  margin-bottom: 1em;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.arrow{
  background-image: url(/gamewave/static/media/arrow-button.8402ea0b.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 3vw;
  height: 3vw;
}
.arrow-left{
  transform: rotate(-180deg);
}
.arrow-down{
  transform: rotate(90deg);
}
.space-button{
  background-image: url(/gamewave/static/media/space-buttom.834ae074.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 100%;
  height: 3vw;
  margin-bottom: 1em;
}
h1{
  margin-bottom: 2em;
}

.rubik-wrapper{
  display: flex;
}

.rubik-buttons{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 50vh;
  height: 70vh;
  padding: 0 1em 0 1em;
  font-weight: bold;
  background-color: #000000AA;
}

.rubik-buttons button {
  background-color: transparent;
  height: 2.2em;
}
.rubik-buttons .blue-button{
  color: #00D2FF;
  border: 0.1em solid #00D2FF;
}
.rubik-buttons .red-button{
  color: #BB0000;
  border: 0.1em solid #BB0000;
}
.rubik-buttons .white-button{
  color: #F2F3EE;
  border: 0.1em solid #F2F3EE;
}
.rubik-buttons .orange-button{
  color: #F0750F;
  border: 0.1em solid #F0750F;
}
.rubik-buttons .yellow-button{
  color: #FFF77D;
  border: 0.1em solid #FFF77D;
}
.rubik-buttons .green-button{
  color: #5A8D03;
  border: 0.1em solid #5A8D03;
}

.chessGameBoard{
  background-color: red;
  width: 80vh;
  height: 80vh;
  display: grid;
  grid-template: repeat(8, 1fr) / repeat(8, 1fr);
}
.cell{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.light{
  background-color: #F5E6BF;
}
.darker {
  background-color: #66443A;
}

